                }
                ul.mega-menu {
                    background-color: #fff;
                    display: block;
                    left: 100%;
                    padding: 40px 40px 0px 40px;
                    position: absolute;
                    text-align: left;
                    top: 0;
                    transform: rotateX(90deg);
                    transform-origin: center top 0;
