                        top: -50px;
                    }
                    @media #{$xl-layout}{
                        top: -50px;
                    }
                    @media #{$lg-layout}{
                        top: -80px;
                    }
                    > li {
                        display: inline-block;
                        float: left;
                        margin-left: 0;
                        padding: 0;
                        width: 33.33%;
                        ul {
                            li.mega-menu-title {
                                float: inherit;
                                width: 100%;
                                a {
                                    color: #242424;
                                    margin: 0 0 10px;
                                    text-transform: uppercase;
                                    font-size: 14px;
                                    font-weight: 500;
                                    &::before {
                                        display: none
                                    }
                                }
                                &:hover a {
                                    padding-left: 0px;
                                }
                            }
                            li {
                                display: block;
                                float: inherit;
                                padding: 0px;
                                a {
                                    color: #333;
                                    display: block;
                                    font-size: 13px;
                                    font-weight: 400;
                                    line-height: 32px;
                                    text-align: left;
                                    text-transform: capitalize;
                                    padding-top: 0px;
                                    &:hover {
                                        color: $theme-color;
                                    }
                                    &::before {
                                        background: $theme-color none repeat scroll 0 0;
                                        border-radius: 50%;
                                        content: "";
                                        height: 7px;
                                        left: 0px;
                                        opacity: 0;
                                        position: absolute;
                                        top: 13px;
                                        transition: all 0.3s ease-in-out 0s;
                                        width: 7px;
                                        z-index: 999;
                                    }
                                }
                                &.mega-menu-img {
                                    &:hover a {
                                        padding-left: 0px;
                                    }
                                    a{
                                       &::before {
                                            display: none;
                                        } 
                                    }
                                }
                                &:hover a::before {
                                    opacity: 1;
                                }
                                &:hover a {
                                    padding-left: 12px;
                                }
                            }
                        }
                    }
                    &.mega-menu-padding {
                        padding: 40px 40px 36px 40px;
                    }
                }
                &:hover > ul.mega-menu {
                    transform: rotateX(0deg);
                    visibility: visible;
                }
                a{
