                    font-size: 12px;
                    font-weight: 400;
                    padding: 5px 0;
                }
  
                &.menu-item-has-children{
                    .menu-expand{
                        height: 30px;
                        top: -15px;
                        margin-top: 0;
                    }
                }
            }
  
            
        }
  
        ul{
            &.sub-menu{
                & > li{
                   & >  a{
                         padding: 5px 0;
                     }
                 }
             }
        }
  
  
    }
  }
  
  
.transparent-bar {
	left: 0;
	right: 0;
	position: absolute;
	width: 100%;
    z-index: 999;
    
    @media #{$md-layout, $xs-layout, $sm-layout}{
        position: relative;
    }

    .header-top{
        display: none !important;
    }
}


.mobile-menu-middle{
    margin-bottom: 50px;
    .lang-curr-style{
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
        button{
            background: none;
            border: none;
        }

       
        .title{
            display: inline-block;
            font-weight: 500;
        }

        select{
            border: 1px solid #ddd;
            padding: 5px 0;
        }

        ul{
            li{
                button{
                    font-size: 12px;
                    &:hover, &.active{
                        color: $theme-color;
                    }
                }
            }
        }
    }
}